import { Toaster } from "sonner";
import type { Metadata } from "next";
import { Roboto } from "next/font/google";  // Importing Roboto font

import "./globals.css";

// Import the font (Roboto) from Google Fonts
const roboto = Roboto({
  variable: "--font-roboto",   // Custom variable for this font
  subsets: ["latin"],
});

// Update metadata
export const metadata: Metadata = {
  title: "PrepWise",
  description: "An AI-powered platform for preparing for mock interviews",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en" className="dark">
      <head>
        {/* Meta tag for viewport */}
        <meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1, minimum-scale=1" />
      </head>
      <body className={`${roboto.className} antialiased pattern`}>
        {children}
        <Toaster />
      </body>
    </html>
  );
}
